<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('@/assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Documentos en línea</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Categoría > Subcategoría > Tipo de pago > Tarjeta</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ operation.document_name }}</div>

          <div class="call-to-action">
            <div class="text text-center" style="display: block">Precio final: S/. {{ operation.price }}</div>
          </div>


          <div class="page-title">Ingresar los datos de tu tarjeta</div>

          <div class="call-to-action" @click="goToBank()">
            <div class="text" style="display: block">
              <v-text-field
                  counter="25"
                  label="Numero de tarjeta *"
              ></v-text-field>

              <v-text-field
                  label="Nombre del titular *"
              ></v-text-field>

              <v-text-field
                  label="Vencimiento *"
                  hint="MM/AA"
                  persistent-hint
              ></v-text-field>

              <v-text-field
                  label="Codigo de seguridad *"
                  hint="Últimos 3 números en el reverso"
                  persistent-hint
              ></v-text-field>
            </div>
          </div>

          <div class="text-center" style="margin: 2rem 0">
            <v-btn color="primary" width="160" @click="goToConfirm()">
              Continuar
              <v-img :src="require('@/assets/icons/lawyer-04.svg')" height="20" contain/>
            </v-btn>
          </div>

        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import axios from "axios";

export default {
  name: 'DocumentVariablePaymentOnline',
  data: () => ({
    isLoading: false,
    operation: {
      lawyer_specialty_id: {},
      ubigeo_department_id: {},
      document: '',
      lawyer: ''
    },
  }),
  computed: {
    ...mapGetters(['user']),
    operationId() {
      return this.$route.params.id
    }
  },
  mounted() {
    this.loadOperation()
  },
  methods: {
    loadOperation() {
      axios.get('operations/' + this.operationId).then((response) => {
        this.operation = response.data
      })
    },
    goToConfirm() {
      this.isLoading = true

      let url = 'operations/' + this.operationId;
      let data = {
        payment_method_id: 1,
        status_id: 5,
        coupon_code: this.operation.coupon_code,
      }

      axios.put(url, data).then((response) => {
        this.$router.push({name: 'DocumentVariablePaymentConfirmed', params: {id: response.data.id}})
      }).catch(() => {
        this.notyf.error('Ups! Ocurrio un error, por favor intentelo mas tarde.');
      }).then(() => {
        this.isLoading = false
      })
    },
  }
}
</script>
